.MainContainer {
        justify-content: center;
        min-width:1200px;
        .list {
            display: flex;
            flex-wrap: wrap;
            justify-content: flex-start;
            gap: 24px;
            width: 100%;
            li {
                list-style-type: none;
}
        }
}
p {
    color: #767E94;
    text-align: center;
    margin-top: 50px;
}

.LastButton {
    display: flex;
    align-content: center;
    align-items: center;
    flex-direction: column;
    margin-top: 64px;
}

.LastMessage {
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 32px;
    text-align: center;
    color: #767E94;
}
button {
    cursor: pointer;
    margin-top: 40px;
    font-size: 14px;
    line-height: 24px;
    background: #7B6EF6;
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 16px 32px;
    gap: 8px;
    border-radius: 12px;
    color:#FFF;
  p {
    transition: 0.2s all;
    cursor: pointer;
    &:hover{
        color: whitesmoke;

    }
  }
}
.MainText {
    margin-top: 24px;
    margin-bottom: 24px;
    margin-left:140px;
}

.Category {
    font-weight: 600;
    font-size: 32px;
    line-height: 40px;
    letter-spacing: -0.02em;
    color: #767E94;
}

.CategoryNum {
    margin-left: 10px;
    font-weight: 600;
    font-size: 16px;
    line-height: 40px;
    letter-spacing: -0.02em;
    color: #767E94;
}
